.search {
  top: 100px;
  position: absolute;
  color: rgb(72, 72, 72);
  background-color: white;
  width: 30%;
  padding: 50px 0px 50px;
  font-size: 20px;
  border-radius: 5px;

  img {
    margin-left: 30px;
  }
  h1 {
    font-size: 30px;
    font-weight: bolder;
  }
  p {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 5px;
  }

  input {
    border-radius: 3px;
    border: #c9c9c9 1px solid;
    width: 100%;
    height: 45px;
    padding: 10px 5px;
  }
  .btn {
    height: 25%;
    width: 25%;
    margin-top: 20px;
    justify-self: center;
    font-weight: bolder;
    color: white;
    background-color: #ff5a5f;
  }

  .container-fluid {
    width: 90%;
  }
}